import oddTypeList from '../utils/oddTypeList'
import sportList from '../utils/sportList'
import mongoose from 'mongoose'
const Schema = mongoose.Schema

export const EventOddSchema = new Schema({
	uniqueId: { type: String},
	singlePickId: { type: String },
	radioPickId: { type: String },
	source: {
		provider: { type: String },
		bookmaker: { type: String },
		id: { type: String },
		lastUpdated: { type: Date }
	},
	sport: { type: String, enum: sportList, required: true },
	oddType: { type: String, enum: oddTypeList, required: true },
	league: { type: String },
	region: { type: String },
	details: { type: String },
	matchTime: { type: Date, required: true },
	cutOffTime: { type: Date },
	team: {
		home: { type: String, required: true },
		homeROT: { type: String },
		homePitcher: { type: String },
		away: { type: String, required: true },
		awayROT: { type: String },
		awayPitcher: { type: String }
	},
	odds: {
		moneyLineHome: { type: Number, default: 0 },
		moneyLineAway: { type: Number, default: 0 },
		pointSpreadHome: { type: Number, default: 0 },
		pointSpreadAway: { type: Number, default: 0 },
		pointSpreadHomeLine: { type: Number, default: 0 },
		pointSpreadAwayLine: { type: Number, default: 0 },
		totalNumber: { type: Number, default: 0 },
		overLine: { type: Number, default: 0 },
		underLine: { type: Number, default: 0 },
		drawLine: { type: Number, default: 0 }
	},
	betDetail: {
		oddLine: { type: Number },
		oddPoint: { type: Number },
		oddTarget: { type: String, enum: [ 'Home', 'Away', 'Over', 'Under', 'Draw' ] },
		betType: { type: String, enum: [ 'M-Line', 'Spread', 'Total', 'Draw' ] }
	},
	score:{
		homeScore: { type: Number, default: 0, required: true },
		awayScore: { type: Number, default: 0, required: true }
	},
	status: { type: String, enum: [ 'Existed', 'Pending', 'TimeOut', 'HasUpdated', 'Won', 'Won Half', 'Lost', 'Lost Half', 'Push', 'Closed', 'Canceled', 'Postponed', 'Review', 'Finished' ] },
	// final: { type: Boolean }, // for result use only
	note: { type: String },
	updatedAt: { type: Date },
	expireAt: { type: Date }
})

EventOddSchema.index({ 'expireAt': 1 }, { expireAfterSeconds: 0 })

export const Result = mongoose.model('Result', EventOddSchema)
export const EventOdd = mongoose.model('EventOdd', EventOddSchema)